import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    card: {
      id: "",
      cardId: "",
      available: 0,
      pack: 0,
      owner: {
        id: "",
        name: "",
        phone: ""
      },
      logs: [],
      createdAt: "",
      updatedAt: ""
    },
    deleteDialog: false,
    createDialog: false,
    updateDialog: false,
    reloadDialog: false,
    cards: [],
    packs: []
  },
  getters: {
    isOpenDelete: (state) => {
      return state.deleteDialog;
    },
    isOpenCreate: (state) => {
      return state.createDialog;
    },
    isOpenUpdate: (state) => {
      return state.updateDialog;
    },
    isOpenReload: (state) => {
      return state.reloadDialog;
    },
    cardList: (state) => {
      return state.cards;
    },
    packList: (state) => {
      return state.packs;
    },
    cardToUpdate: (state) => {
      return state.card;
    }
  },
  mutations: {
    changeDeleteDialog(state, payload) {
      state.deleteDialog = payload.dialog;
    },
    changeCreateDialog(state, payload) {
      state.createDialog = payload.dialog;
    },
    changeUpdateDialog(state, payload) {
      state.updateDialog = payload.dialog;
    },
    changeReloadDialog(state, payload) {
      state.reloadDialog = payload.dialog;
    },
    SET_CARDS(state, payload) {
      state.cards = payload;
    },
    ADD_CARD(state, payload) {
      state.cards.unshift(payload);
    },
    CARD_TO_UPDATE(state, payload) {
      const {
        id, cardId, available, pack,
        owner: { id: ownerId, name, phone },
        logs, createdAt, updatedAt
      } = payload;
      state.card = {
        id, cardId, available, pack,
        owner: { id: ownerId, name, phone },
        logs, createdAt, updatedAt
      };

    },
    UPDATED_CARD(state, payload) {
      state.cards = state.cards.map((c) => {
        if (c.id === payload.id) c = payload;
        return c;
      });
    },
    DELETE_CARD(state, payload) {
      state.cards = state.cards.filter((c) => c.id !== payload.id);
    },
    SET_PACKS(state, payload) {
      state.packs = payload;
    }
  },
  actions: {
    getCards({ commit }) {
      axios.get('/cards')
        .then(({ data, status }) => {
          if (status === 200) commit('SET_CARDS', data);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getPacks({ commit }) {
      axios.get('/pack')
        .then(({ data, status }) => {
          if (status === 200) commit('SET_PACKS', data);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    ownerCard({ commit }, payload) {
      let newOwner = {};
      if (payload.name) newOwner.name = payload.name;
      if (payload.phone) newOwner.phone = payload.phone;
      console.log('UP ', payload);
      axios.put(
        `owner/cards/${payload.cardId}`,
        { ...newOwner }
      )
        .then(({ data, status }) => {
          if (status === 200) commit('UPDATED_CARD', data);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    reloadCard({ commit }, { cardId, packId }) {
      axios.put(
        `reload/cards/${cardId}`,
        { packId }
      )
        .then(({ data, status }) => {
          console.log({ data, status });
          if (status === 200) commit('UPDATED_CARD', data);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    addCard({ commit }, { cardId, packId, owner: newOwner }) {
      let data = { cardId, packId };
      let owner = {};
      if (newOwner.name) owner.name = newOwner.name;
      if (newOwner.phone) owner.phone = newOwner.phone;
      if (Object.keys(owner).length > 0) data.owner = { ...owner };
      console.log('OWNER ', data);
      axios.post('/cards', data)
        .then(({ data, status }) => {
          if (status === 201) commit('ADD_CARD', data);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    deleteCard({ commit }, { id }) {
      axios.delete(`/cards/${id}`)
        .then(({ data, status }) => {
          if (status === 200) commit('DELETE_CARD', data);
        })
        .catch((err) => {
          console.log(err);
        });
    }
  },
  modules: {}
})
